export const POST_TEXT_MAX_LENGTH = 1024;
export const POST_TEXT_MAX_LENGTH_NO_MEDIA = 4096;

export const VALID_IMAGE_EXTENSIONS: string[] = ['png', 'jpeg', 'gif', 'jpg'];
export const VALID_VIDEO_EXTENSIONS: string[] = ['mp4', 'mov', 'quicktime'];

export const sizeError = (size: string, names: string[]) =>
    `${names.length === 1 ? 'Файл' : 'Файлы'} ${names.join(', ')} ${names.length === 1 ? 'превысил' : 'превысили'} максимальный размер в ${size}`;

export const extensionError = (extensions: string[], names: string[]) =>
    `${names.length === 1 ? 'Файл' : 'Файлы'} ${names.join(', ')} не ${names.length === 1 ? 'подходит' : 'подходят'} под типы ${extensions.join(', ')}`;
