import {
  POST_TEXT_MAX_LENGTH,
  POST_TEXT_MAX_LENGTH_NO_MEDIA,
} from '../../features/Post/constants';

export const mapTabToStatusLabel: Record<any, string> = {
  archive: 'в архиве',
  queue: 'в очереди',
  storage: 'в хранилище',
};

export const postStatusRus: Record<any, string> = {
  generating: 'на генерации',
  new: 'новая',
  moderation: 'на модерации',
  planned: 'в очереди',
  skipped: 'пропущена',
  published: 'в архиве',
};

export const getCurrentText = (post: any, usedVersion: number | null): string =>
  String(
    post.versions.find((version) => version.id === usedVersion)?.text ?? ''
  );

export const getMaxTextLength = (post: any): number =>
  post.attachments.length > 0
    ? POST_TEXT_MAX_LENGTH
    : POST_TEXT_MAX_LENGTH_NO_MEDIA;

export const getFileExtension = (file: File): string | null => {
  const parts = file.name.split('.').filter(Boolean);

  return parts.at(-1) ?? null;
};

export const isManualPostVersion = (post: any, usedVersion: number): boolean =>
  post.versions.find((version) => version.id === usedVersion)?.manual ?? false;

export const getNewPostInvalidFields = (payload): any => {
  const invalidFields: any = {};

  if (payload.channels.length === 0) {
    invalidFields.channels = 'Выберите канал для публикации поста';
  }

  if (payload.baseText.trim().length === 0) {
    invalidFields.baseText = 'Укажите текст поста';
  }

  return invalidFields;
};
